h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'vezitsa';
}
.display2 {
  font-size: 2.8125em;
  line-height: 105%;
  font-weight: 400;
  color: #FFFFFF;
  display: inline-flex;
  flex-direction: column;
  position: relative;
  span {
  }
  i {
    display: flex;
    align-items: center;
    margin-top: 0.3125em;
    position: relative;
    svg {
      position: relative;
      z-index: 1;
    }
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      right: 0;
      transform: translate(0, -50%);
      width: 100%;
      height: 1px;
      background: linear-gradient(45deg, rgb(251, 162, 54) 100%, rgb(255, 208, 151) 100%);
    }
  }
}
.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
}
.btn-green {
  border: 0.0625em solid #336315;
  filter: drop-shadow(0 0 1.25em rgba(0, 0, 0, 0.67));
  border-radius: 3.125em;
  background: linear-gradient(180deg, #203911 0%, #223815 100%);
  position: relative;
  overflow: hidden;
  span {
    font-size: 0.9375em;
    line-height: 120%;
    font-weight: 500;
    color: #EDEDED;
  }
  &:hover {
    background: linear-gradient(180deg, #254212 0%, #385f20 100%);
  }
  &:after {
    content: '';
    position: absolute;
    z-index: 0;
    left: 0.75em;
    bottom: -0.9375em;
    width: 7.0625em;
    height: 2.375em;
    background: #646961;
    opacity: 0.6;
    filter: blur(1.0625em);
    pointer-events: none;
  }
}
.btn-orange {
  border: 0.0625em solid #FBA236;
  filter: drop-shadow(0 0 1.25em rgba(0, 0, 0, 0.67));
  border-radius: 3.125em;
  background: linear-gradient(180deg, #FBA02F 0%, #C52E0D 100%);
  position: relative;
  overflow: hidden;
  span {
    font-size: 0.9375em;
    line-height: 120%;
    font-weight: 500;
    color: #0D1509;
  }
  &:hover {
    background: linear-gradient(180deg, #ffaa48 0%, #dc3712 100%);
  }
  &:after {
    content: '';
    position: absolute;
    z-index: 0;
    left: 0.75em;
    bottom: -0.9375em;
    width: 7.0625em;
    height: 2.375em;
    background: #EDFA7C;
    opacity: 0.6;
    filter: blur(1.0625em);
    pointer-events: none;
  }
}
.swiper-button-next,
.swiper-button-prev {
  z-index: 1;
}

@media screen and (max-width: 992px) {
  .display2 {
    font-size: 2.25em;
    i svg {
      width: 0.95em;
      height: 0.95em;
    }
  }
}

@media screen and (max-width: 767px) {
  .display2 {
    font-size: 1.75em;
  }
}