.main {
  background-image: url("../img/main-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 9.125em 0;
  position: relative;
  overflow: hidden;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 45%;
    transform: translate(-50%, 0);
    width: 21.875em;
    height: 17.6875em;
    background: #FBA236;
    opacity: 0.8;
    filter: blur(12.875em);
  }
  &__inner {
    display: inline-block;
    padding: 2.3125em 5.3125em 2.3125em 0;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      width: 625em;
      height: 100%;
      background: rgba(18, 28, 12, 0.83);
    }
  }
  &__content {
    max-width: 32.1875em;
    position: relative;
    z-index: 2;
  }
  &__title {
    font-size: 3.4375em;
    line-height: 120%;
    font-weight: 400;
    color: #FFFFFF;
  }
  &__text {
    margin-top: 1.5em;
    p {
      font-size: 1.3125em;
      line-height: 140%;
      color: #FFFFFF;
    }
  }
  &__btn {
    height: 4em;
    padding: 0 2.125em;
    margin-top: 1.5em;
  }
}
.hunting {
  padding: 6.25em 0;
  position: relative;
  overflow: hidden;
  &__bg {
    span:nth-child(1) {
      position: absolute;
      z-index: 0;
      top: 18.75em;
      left: 0;
      width: 100%;
      height: 36.25em;
      background: linear-gradient(180deg, rgba(18, 28, 12, 0) 0%, #33422A 48.96%, rgba(18, 28, 12, 0) 100%);
      pointer-events: none;
    }
    span:nth-child(2) {
      position: absolute;
      z-index: 0;
      top: 60em;
      left: 0;
      width: 100%;
      height: 36.25em;
      background: linear-gradient(180deg, rgba(18, 28, 12, 0) 0%, #33422A 48.96%, rgba(18, 28, 12, 0) 100%);
      pointer-events: none;
    }
    span:nth-child(3) {
      position: absolute;
      z-index: 0;
      top: 100em;
      left: 0;
      width: 100%;
      height: 36.25em;
      background: linear-gradient(180deg, rgba(18, 28, 12, 0) 0%, #33422A 48.96%, rgba(18, 28, 12, 0) 100%);
      pointer-events: none;
    }
  }
  &__grid {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 18.5625em / 4));
    gap: 5.0625em 6.1875em;
    margin-top: 3.125em;
    position: relative;
    z-index: 1;
  }
}
.hunting-card {
  position: relative;
  &:hover {
    .hunting-card__wrapp {
      background: linear-gradient(180deg, rgba(111, 116, 108, 0) 0%, rgba(111, 116, 108, 0.85) 100%);
    }
  }
  &__bg {
    position: absolute;
    top: 1.25em;
    right: 0.625em;
    max-width: 21.9375em;
    pointer-events: none;
  }
  &__wrapp {
    background: linear-gradient(180deg, rgba(111, 116, 108, 0) 0%, rgba(111, 116, 108, 0.65) 100%);
    border: 0.0625em solid rgba(251, 162, 54, 0.7);
    border-radius: 3.125em;
    padding: 2.125em 0.9375em 2.75em;
    text-align: center;
  }
  &__title {
    min-height: 4.75em;
    span {
      font-family: 'vezitsa';
      font-size: 2em;
      line-height: 120%;
      color: #FFFFFF;
    }
  }
  &__images {
    width: 12.25em;
    height: 12.25em;
    position: relative;
    margin: 0.625em auto 0;
    img {
      position: absolute;
      z-index: 1;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: auto;
      max-width: 14.0625em;
    }
    .deer {
      left: unset !important;
      right: 0 !important;
      transform: unset !important;
      max-width: 16.75em !important;
    }
  }
  &__price {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.625em;
    span:nth-child(1) {
      font-size: 1em;
      line-height: 142%;
      color: #FFFFFF;
    }
    span:nth-child(2) {
      font-family: 'vezitsa';
      font-size: 2em;
      line-height: 120%;
      color: #FFFFFF;
    }
  }
  .line {
    position: relative;
    margin-top: 1.6875em;
    margin-bottom: 0.625em;
    padding-bottom: 0.625em;
    &:after {
      content: '';
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: 9.25em;
      height: 0.0625em;
      background: linear-gradient(45deg,#fba236 100%,#ffd097 100%);
    }
  }
  &__btn {
    width: 13.5em;
    height: 3.4375em;
    margin: -2em auto 0;
    span {
      font-size: 1.125em !important;
    }
  }
}
.wrap {
  overflow: hidden;
}
.season {
  background-image: url("../img/season-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 80%;
  position: relative;
  &__bg {
    position: absolute;
    top: -24.5625em;
    right: 0;
    pointer-events: none;
  }
  &__inner {
    position: relative;
  }
  &__top {
    display: flex;
    justify-content: space-between;
  }
  &__content {
    margin-top: 6.5625em;
  }
  &__select {
    position: absolute;
    z-index: 2;
    left: 2.9375em;
    bottom: 2.4375em;
    width: 12.5em;
  }
  .sliders {
    display: none;
    &--active {
      display: block;
    }
  }
  .slider__big {
    .swiper-wrapper {
      align-items: stretch;
    }
    .swiper-slide {
      height: auto;
      display: flex;
      align-items: flex-end;
      img {
        max-width: 100%;
        width: auto;
      }
    }
  }
  .slider__small-wrapp {
    background: rgba(111, 116, 108, 0.65);
    border: 0.0625em solid rgba(251,162,54,.7);
    border-radius: 3.125em;
    padding: 2em 7em 2em 21.875em;
    position: relative;
    .swiper-wrapper {
      align-items: center;
    }
    .swiper-slide {
      text-align: center;
    }
  }
  &-slide {
    &__title {
      display: inline-block;
      position: relative;
      padding-left: 1.25em;
      &:after {
        content: '';
        position: absolute;
        top: 0.5em;
        left: 0;
        width: 0.625em;
        height: 0.625em;
        background: linear-gradient(180deg, #FBA236 0%, #FFD097 100%);
        border-radius: 50%;
      }
      span {
        font-size: 1.5em;
        line-height: 120%;
        color: #FBA236;
      }
    }
  }
  .select__wrap {
    position: relative;
    &--active {
      .select__list {
        display: block;
      }
    }
  }
  .select__list-default {
    display: flex;
    align-items: center;
    padding: 0.3125em 0;
    cursor:pointer;
    li {
      font-family: 'Vezitsa';
      font-size: 2em;
      line-height: 120%;
      color: #FFFFFF;
    }
    .select-arrow {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-left: 0.625em;
    }
  }
  .select__list {
    position: absolute;
    z-index: 5;
    top: 100%;
    left: 0;
    width: 100%;
    background: #ffffff;
    display: none;
    .li--active {
      background: #d9d9d9;
    }
    li {
      font-size: 1.25em;
      line-height: 105%;
      padding: 0.4375em 0.625em;
      cursor: pointer;
      &:hover {
        background: #d9d9d9;
      }
    }
  }
  .swiper-button-prev {
    left: 19em;
    transform: rotate(180deg);
    &:after {
      display: none;
    }
  }
  .swiper-button-next {
    right: 3.125em;
    &:after {
      display: none;
    }
  }
}
.dates {
  padding: 6.25em 0;
  &__text {
    margin-top: 3.125em;
    p {
      font-size: 1.5em;
      line-height: 140%;
      color: #FFFFFF;
      &:not(:first-child) {
        margin-top: 2.5em;
      }
      a {
        text-decoration-line: underline;
        color: #FBA236;
      }
    }
  }
}
.info {
  background-image: url("../img/info-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-bottom: 10.625em;
  .right-text {
    padding-right: 11.875em;
    text-align: right;
    span:nth-child(1) {
      display: block;
      font-family: 'vezitsa';
      font-size: 6.5625em;
      line-height: 100%;
      color: #FFFFFF;
    }
    span:nth-child(2) {
      display: block;
      font-family: 'vezitsa';
      font-size: 4.375em;
      line-height: 100%;
      color: #FFFFFF;
    }
  }
  .left-text {
    margin-top: 2.1875em;
    span:nth-child(1) {
      display: block;
      font-family: 'vezitsa';
      font-size: 9.375em;
      line-height: 100%;
      color: #FFFFFF;
    }
    span:nth-child(2) {
      display: block;
      font-family: 'vezitsa';
      font-size: 6.25em;
      line-height: 100%;
      color: #FFFFFF;
    }
  }
}
.workshop {
  padding: 10.625em 0 5.3125em;
  &__info {
    max-width: 50.625em;
    margin-top: 1.875em;
    p {
      font-size: 1.3125em;
      line-height: 140%;
      color: #FFFFFF;
    }
  }
  &__slider {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    margin-top: 3.125em;
    &-left {
      width: 69.625em;
    }
    &-right {
      width: calc(100% - 69.625em);
      position: relative;
      text-align: right;
    }
    ._after {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 37.875em;
      height: 37.875em;
      background: #25311D;
      filter: blur(8.28125em);
      pointer-events: none;
    }
    &-nav {
      display: flex;
      align-items: center;
      margin-top: 8.75em;
    }
  }
  &__right-wrap {
    display: inline-flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  &__slide {
    img {
      width: 100%;
    }
  }
  &__btn {
    height: 4em;
    padding: 0 1.25em;
  }
  .swiper-button-prev {
    position: static;
    &:after {
      display: none;
    }
  }
  .swiper-button-next {
    margin-left: 2.5em;
    position: static;
    &:after {
      display: none;
    }
  }
}
.huntsman {
  padding: 5.3125em 0 17.5em;
  position: relative;
  overflow: hidden;
  &__bg {
    position: absolute;
    z-index: 0;
    top: 15em;
    left: 0;
    width: 58.75em;
    pointer-events: none;
  }
  &__content {
    margin-top: 10.125em;
    position: relative;
    background: rgba(111, 116, 108, 0.65);
    border: 0.0625em solid rgba(251, 162, 54, 0.7);
    border-radius: 3.125em;
    max-width: 68.0625em;
    margin-left: auto;
    padding: 2.6875em 2.6875em 3.875em 16.0625em;
    ._after {
      position: absolute;
      z-index: -1;
      top: 0;
      left: 10.375em;
      width: 71.5em;
      height: 100%;
      background: linear-gradient(180deg, rgba(18, 28, 12, 0) 6.64%, #33422A 55.6%, rgba(18, 28, 12, 0) 106.64%);
    }
  }
  &__img {
    position: absolute;
    top: 50%;
    left: -16.0625em;
    transform: translate(0, -50%);
    width: 30.625em;
    img {
      width: 100%;
    }
  }
  &__name {
    font-family: 'vezitsa';
    font-size: 2.125em;
    line-height: 105%;
    color: #FFFFFF;
  }
  &__experience {
    margin-top: 1.875em;
    p {
      font-size: 1.0625em;
      line-height: 140%;
      color: #FFFFFF;
      span {
        font-size: 1.25em;
        line-height: 140%;
        color: #FFB863;
      }
    }
  }
  &__info {
    margin-top: 0.8125em;
    p {
      font-size: 1.25em;
      line-height: 140%;
      color: #FFFFFF;
    }
  }
  &__btn {
    position: absolute !important;
    left: 16.0625em;
    bottom: -2em;
    height: 4em;
    padding: 0 2em;
  }
}
.hall {
  padding: 3.125em 0 4.6875em;
  &__grid {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 5.625em / 4));
    gap: 1.875em;
    margin-top: 1.5625em;
  }
  &__item {
    &-img {
      height: 100%;
      img {
        display: block;
        width: 100%;
        height: 100%;
        border-radius: 3.125em;
      }
    }
    &-text {
      height: 100%;
      background: rgba(111, 116, 108, 0.65);
      border: 0.0625em solid rgba(251, 162, 54, 0.7);
      border-radius: 3.125em;
      padding: 3.9375em 1.75em;
      position: relative;
      p {
        font-size: 1.25em;
        line-height: 140%;
        color: #FFFFFF;
        span {
          color: #FBA236;
        }
      }
    }
  }
  .big {
    grid-column: 1/3;
  }
  .big2 {
    grid-column: 3/5;
    grid-row: 1/3;
  }
}
.offer {
  position: relative;
  .container {
    padding: 0;
  }
  &__inner {
    padding: 12.25em 0;
    position: relative;
  }
  &__bg {
    position: absolute;
    z-index: 0;
    top: 0;
    right: 9.375em;
    width: 42em;
  }
  &__bg2 {
    position: absolute;
    right: 4.375em;
    bottom: 2em;
    width: 10.625em;
  }
  &__content {
    position: relative;
    ._after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      width: 625em;
      height: 100%;
      background: rgba(111, 116, 108, 0.4);
      border: 0.0625em solid rgba(251,162,54,.7);
      pointer-events: none;
    }
  }
  &__wrapp {
    position: relative;
    z-index: 2;
    padding: 2.8125em 41em 2.8125em 3.125em;
    overflow: hidden;
    &:after {
      content: '';
      position: absolute;
      z-index: 1;
      top: 50%;
      right: -39em;
      transform: translate(0, -50%);
      width: 43.375em;
      height: 43.375em;
      background: #121C0C;
      border: 0.0625em solid rgba(251,162,54,.7);
      border-radius: 50%;
    }
    &:before {
      content: '';
      position: absolute;
      top: 50%;
      right: 11.4375em;
      transform: translate(0, -50%);
      width: 14.4375em;
      height: 14.4375em;
      background: #FBA236;
      border-radius: 50%;
      filter: blur(13.21875em);
    }
  }
  &__info {
    margin-top: 0.625em;
    span {
      font-size: 1.25em;
      line-height: 140%;
      color: #FBA236;
    }
  }
  &__price {
    margin-top: 1.5625em;
    p {
      font-size: 1.25em;
      line-height: 180%;
      color: #FFFFFF;
    }
    .text-orange {
      color: #FBA236;
    }
    .text-gray {
      display: block;
      color: rgba(255, 255, 255, 0.5);
    }
  }
  &__btn {
    height: 4em;
    padding: 0 2.5em;
    margin-top: 1.5625em;
  }
  &__img {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 39.875em;
    img {
      width: 100%;
    }
  }
}
.rules {
  background-image: url("../img/rules-bg.webp");
  background-repeat: no-repeat;
